.card{
    height: 170px;
    width: 170px;

    margin-top: 30px;
    margin-right: 30px;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;

    background: -webkit-gradient(linear, left top, right top, color-stop(50%, #aa137a), color-stop(50%, #0beaf9));

    background: -webkit-linear-gradient(left, #aa137a 50%, #0beaf9 50%);

    background: -o-linear-gradient(left, #aa137a 50%, #0beaf9 50%);

    background: linear-gradient(90deg, #aa137a 50%, #0beaf9 50%);
}

.imgOfCard{
    height: 110px;

    display:-ms-flexbox;

    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.imgOfCard .img, .imgOfCard .img img{
    width: 110px;
}

.textOfCard{ 
    text-align: center;
    text-decoration: none;
    background-color: rgba(0,0,0,0.4);
}

.textOfCard .headCard{
    font-style: italic;
    font-weight: bold;
    font-size: 14px;
    
    color: #ffffff;
}


